@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --ease: cubic-bezier(0.75, 0, 0.175, 1);
  --gutter: 20px;
  --wrapper: 1920px;
  --wrapper-narrow: 720px;
  --focus-outline: none;
  --block-gap: 4.5rem;
  --block-header-alignment: center;
  --block-margin: 2.25rem;
  --block-padding: 3rem;
  --duration-s: 125ms;
  --duration-m: 250ms;
  --duration-l: 500ms;
  --line-height-s: 1;
  --line-height-m: 1.3;
  --line-height-l: 1.6;
  --font-1: "IBM Plex Sans", system-ui, sans-serif;
  --font-2: "IBM Plex Sans Condensed", system-ui, sans-serif;
  --font-3: "Söhne Schmall", system-ui, sans-serif;
  --font-size-xs: 0.75rem; /* 12px */
  --font-size-s: 0.875rem; /* 14px */
  --font-size-m: 1rem; /* 16px */
  --font-size-l: 1.125rem; /* 18px */
  --font-size-xl: 2.5rem; /* 32px */
  --font-size-xxl: 4rem; /* 48px */
  --bg-color: 0 0 0;
  --bg-color-tint: 0 0 0;
  --border-color: 31 51 106 / 10%;
  --color-1: 31 51 106;
  --text-color: 31 51 106 / 85%;
  --text-color-hc: 31 51 106;
  --text-color-lc: 31 51 106 / 50%;
  --link-color: var(--text-color);
  --link-color-hover: var(--text-color-hc);
  /**
   * Typography
   */
  --h1-color: var(--text-color-hc);
  --h1-font: var(--font-3);
  --h1-font-size: 64px;
  --h1-font-weight: 700;
  --h1-letter-spacing: 0.01em;
  --h1-line-height: var(--line-height-s);
  --h1-text-transform: uppercase;
  --h2-color: var(--text-color-hc);
  --h2-font: var(--font-2);
  --h2-font-size: 14px;
  --h2-font-weight: 700;
  --h2-letter-spacing: 0.01em;
  --h2-line-height: var(--line-height-s);
  --h2-text-transform: uppercase;
  --intro-color: var(--text-color-hc);
  --intro-font: var(--font-2);
  --intro-font-size: var(--font-size-l);
  --intro-font-weight: 500;
  --intro-letter-spacing: 0;
  --intro-line-height: var(--line-height-m);
  --intro-text-transform: none;
  --text-alpha-high: 100%;
  --text-alpha-mid: 75%;
  --text-alpha-low: 50%;
  /**
   * Inputs
   */
  --input-bg-color: 49 80 166 / 50%;
  --input-bg-color-focus: 49 80 166;
  --input-border-color: 255 255 255 / 75%;
  --input-border-color-focus: 255 255 255;
  --input-border-radius: .25rem;
  --input-border-width: 0;
  --input-border-width-focus: 0;
  --input-box-shadow: none;
  --input-box-shadow-focus: none;
  --input-text-color: 255 255 255 / 75%;
  --input-text-color-focus: 255 255 255;
  --input-placeholder-color: 255 255 255 / 50%;
  /**
   * Buttons
   */
  --btn-font: var(--font-1);
  --btn-font-weight: 500;
  --btn-letter-spacing: 0;
  --btn-radius-s: 1.25rem;
  --btn-radius-m: 1.5rem;
  --btn-radius-l: 0px;
  --btn-text-transform: none;
  --btn-bg-color: transparent;
  --btn-bg-color-hover: transparent;
  --btn-border-color: var(--color-1) / 15%;
  --btn-border-color-hover: var(--color-1) / 50%;
  --btn-border-width: 1px;
  --btn-text-color: var(--color-1);
  --btn-text-color-hover: var(--color-1);
  --btn-inverted-bg-color: transparent;
  --btn-inverted-bg-color-hover: transparent;
  --btn-inverted-border-color: 255 255 255 / 25%;
  --btn-inverted-border-color-hover: 255 255 255 / 50%;
  --btn-inverted-border-width: 1px;
  --btn-inverted-text-color: 255 255 255;
  --btn-inverted-text-color-hover: 255 255 255;
  --btn-primary-bg-color: var(--color-1);
  --btn-primary-bg-color-hover: var(--color-1);
  --btn-primary-border-color: var(--color-1);
  --btn-primary-border-color-hover: var(--color-1);
  --btn-primary-border-width: 0;
  --btn-primary-text-color: 255 255 255;
  --btn-primary-text-color-hover: 255 255 255;
  --btn-primary-inverted-bg-color: 255 255 255;
  --btn-primary-inverted-bg-color-hover: 255 255 255;
  --btn-primary-inverted-border-color: 255 255 255;
  --btn-primary-inverted-border-color-hover: 255 255 255;
  --btn-primary-inverted-text-color: 31 51 106;
  --btn-primary-inverted-text-color-hover: 31 51 106;
}

@media (min-width: 1024px) {
  :root {
    --gutter: 5vw;
    --block-gap: 15vh;
    --block-header-alignment: left;
    --block-margin: 3rem;
    --block-padding: 7.5vh;
    --font-size-l: 1.25rem;
    --font-size-xl: 1.25rem; /* 20px */
    --font-size-xxl: 2rem; /* 32px */
    --font-size-3xl: 2.25rem; /* 36px */
    --font-size-4xl: 4rem; /* 48px */
    /**
     * Typography
     */
    --h1-font-size: 96px;
    --h2-font-size: 16px;
  }
}
@media (min-width: 1280px) {
  :root {
    --font-size-xl: 3rem;
    --font-size-xxl: 5rem;
  }
}
/**
 * Themes
 */
.inverted {
  --text-color: 255 255 255 / 85%;
  --text-color-hc: 255 255 255;
  --text-color-lc: 255 255 255 / 50%;
  --link-color: 255 255 255;
  --link-color-hover: 255 255 255;
  --h1-color: 255 255 255;
  --h2-color: 255 255 255;
  --btn-bg-color: var(--btn-inverted-bg-color);
  --btn-bg-color-hover: var(--btn-inverted-bg-color-hover);
  --btn-border-color: var(--btn-inverted-border-color);
  --btn-border-color-hover: var(--btn-inverted-border-color-hover);
  --btn-border-width: var(--btn-inverted-border-width);
  --btn-text-color: var(--btn-inverted-text-color);
  --btn-text-color-hover: var(--btn-inverted-text-color-hover);
  --btn-primary-bg-color: var(--btn-primary-inverted-bg-color);
  --btn-primary-bg-color-hover: var(--btn-primary-inverted-bg-color-hover);
  --btn-primary-border-color: var(--btn-primary-inverted-border-color);
  --btn-primary-border-color-hover: var(--btn-primary-inverted-border-color-hover);
  --btn-primary-border-width: var(--btn-primary-inverted-border-width);
  --btn-primary-text-color: var(--btn-primary-inverted-text-color);
  --btn-primary-text-color-hover: var(--btn-primary-inverted-text-color-hover);
  color: rgb(var(--text-color));
}

/**
 * Client styles
 */
@font-face {
  font-display: swap;
  font-family: "Söhne Schmall";
  font-style: normal;
  font-weight: 700;
  src: url("/assets/fonts/soehne-schmal-fett.woff2") format("woff2");
}
*,
*::after,
*::before {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

a:focus,
button:focus {
  outline: 3px solid rgb(var(--btn-color-bg)/10%);
}

a {
  color: rgb(var(--link-color));
  text-decoration: none;
  text-underline-offset: 2px;
}
a:hover {
  color: rgb(var(--link-color-hover));
}

body {
  background-color: rgb(var(--bg-color-tint));
  color: rgb(var(--text-color));
  font-family: var(--font-1);
  font-weight: 400;
  min-height: 100vh;
  line-height: var(--line-height-m);
  overscroll-behavior: contain;
  scroll-behavior: smooth;
}

button {
  appearance: none;
  background: transparent;
  border: none;
  cursor: pointer;
  font-family: var(--font-1);
  font-size: var(--font-size-m);
  margin: 0;
  padding: 0;
}

h1 {
  color: rgb(var(--h1-color));
  font-family: var(--h1-font);
  font-size: var(--h1-font-size);
  font-weight: var(--h1-font-weight);
  letter-spacing: var(--h1-letter-spacing);
  line-height: var(--h1-line-height);
  text-transform: var(--h1-text-transform);
}

h2 {
  color: rgb(var(--h2-color));
  font-family: var(--h2-font);
  font-size: var(--h2-font-size);
  font-weight: var(--h2-font-weight);
  letter-spacing: var(--h2-letter-spacing);
  line-height: var(--h2-line-height);
  text-transform: var(--h2-text-transform);
}

img,
video {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

@keyframes animateIn {
  from {
    opacity: 0;
    transform: translate3d(0, 1em, 0);
  }
}
.animateIn {
  --delay: 150ms;
  animation: animateIn 1s ease both;
}
.animateIn--delay1 {
  animation-delay: calc(var(--delay) * 1);
}
.animateIn--delay2 {
  animation-delay: calc(var(--delay) * 2);
}
.animateIn--delay3 {
  animation-delay: calc(var(--delay) * 3);
}
.animateIn--delay4 {
  animation-delay: calc(var(--delay) * 4);
}
.animateIn--delay5 {
  animation-delay: calc(var(--delay) * 5);
}
.animateIn--delay6 {
  animation-delay: calc(var(--delay) * 6);
}
.animateIn--delay7 {
  animation-delay: calc(var(--delay) * 7);
}
.animateIn--delay8 {
  animation-delay: calc(var(--delay) * 8);
}
.animateIn--delay9 {
  animation-delay: calc(var(--delay) * 9);
}
.animateIn--delay10 {
  animation-delay: calc(var(--delay) * 10);
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
.btn {
  font-feature-settings: "ss01";
  align-items: center;
  backdrop-filter: blur(8px);
  background-color: rgb(var(--btn-bg-color));
  box-shadow: inset 0 0 0 var(--btn-border-width) rgb(var(--btn-border-color));
  border-radius: var(--btn-radius-m);
  appearance: none;
  color: rgb(var(--btn-text-color));
  cursor: pointer;
  display: inline-flex;
  font-family: var(--btn-font);
  font-weight: var(--btn-font-weight);
  gap: 0.5rem;
  height: 48px;
  justify-content: center;
  letter-spacing: var(--btn-letter-spacing);
  line-height: 20px;
  padding: 1px 1.5rem;
  position: relative;
  text-align: center;
  text-transform: var(--btn-text-transform);
  transition: background-color var(--duration-m) ease, box-shadow var(--duration-m) ease, color var(--duration-m) ease;
  user-select: none;
  white-space: nowrap;
}
.btn:hover {
  background-color: rgb(var(--btn-bg-color-hover));
  box-shadow: inset 0 0 0 var(--btn-border-width) rgb(var(--btn-border-color-hover));
  color: rgb(var(--btn-text-color-hover));
}
.btn:active {
  padding-bottom: 0;
  padding-top: 2px;
}
.btn[type=submit]::after {
  animation: spinner 1s infinite linear;
  background-color: rgb(var(--btn-text-color));
  content: "";
  display: none;
  height: 24px;
  left: calc(50% - 12px);
  mask-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M12,3C7.03125,3,3,7.03125,3,12s4.03125,9,9,9c3.28594,0,6.15469-1.75781,7.72969-4.3875l.00469.00469c-.32344.69844-.07031,1.5375.60938,1.93125.71719.41719,1.63594.16875,2.04844-.54844.00938-.01406.01875-.02813.02344-.04219h0c-2.06719,3.60938-5.95781,6.04219-10.41563,6.04219C5.37187,24,0,18.62812,0,12S5.37187,0,12,0c-.82969,0-1.5.67031-1.5,1.5s.67031,1.5,1.5,1.5Z" isolation="isolate" opacity=".4" stroke-width="0"/><path d="M10.5,1.5c0-.82969.67031-1.5,1.5-1.5,6.62812,0,12,5.37187,12,12,0,2.18438-.58594,4.2375-1.60781,6-.4125.71719-1.33125.96094-2.04844.54844s-.96094-1.33125-.54844-2.04844c.76406-1.32188,1.20469-2.85938,1.20469-4.5,0-4.96875-4.03125-9-9-9-.82969,0-1.5-.67031-1.5-1.5Z" stroke-width="0"/></svg>');
  mask-size: 24px 24px;
  position: absolute;
  top: calc(50% - 12px);
  width: 24px;
}
.is-loading .btn[type=submit], .btn[type=submit].is-loading {
  color: transparent;
  cursor: not-allowed;
  pointer-events: none;
}
.is-loading .btn[type=submit]::after, .btn[type=submit].is-loading::after {
  display: block;
}
.btn--block {
  display: flex;
  width: 100%;
}
.btn--inverted {
  --btn-bg-color: var(--btn-inverted-bg-color);
  --btn-bg-color-hover: var(--btn-inverted-bg-color-hover);
  --btn-border-color: var(--btn-inverted-border-color);
  --btn-border-color-hover: var(--btn-inverted-border-color-hover);
  --btn-border-width: var(--btn-inverted-border-width);
  --btn-text-color: var(--btn-inverted-text-color);
  --btn-text-color-hover: var(--btn-inverted-text-color-hover);
}
.btn--primary {
  --btn-bg-color: var(--btn-primary-bg-color);
  --btn-bg-color-hover: var(--btn-primary-bg-color-hover);
  --btn-border-color: var(--btn-primary-border-color);
  --btn-border-color-hover: var(--btn-primary-border-color-hover);
  --btn-border-width: var(--btn-primary-border-width);
  --btn-text-color: var(--btn-primary-text-color);
  --btn-text-color-hover: var(--btn-primary-text-color-hover);
}
.btn--primary.btn--inverted {
  --btn-bg-color: var(--btn-primary-inverted-bg-color);
  --btn-bg-color-hover: var(--btn-primary-inverted-bg-color-hover);
  --btn-border-color: var(--btn-primary-inverted-border-color);
  --btn-border-color-hover: var(--btn-primary-inverted-border-color-hover);
  --btn-border-width: var(--btn-primary-inverted-border-width);
  --btn-text-color: var(--btn-primary-inverted-text-color);
  --btn-text-color-hover: var(--btn-primary-inverted-text-color-hover);
}
.btn--small {
  border-radius: var(--btn-radius-s);
  height: 2.5rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
}

.contact {
  font-size: var(--font-size-xs);
  line-height: 1.75;
}
.contact a {
  background: linear-gradient(to right, rgba(255, 255, 255, 0.25), rgba(255, 255, 255, 0.25)), linear-gradient(to right, rgb(255, 255, 255), rgb(255, 255, 255));
  background-position: 0 100%, 0 100%;
  background-repeat: no-repeat;
  background-size: 100% 1px, 0 1px;
  transition: background-size 250ms;
}
.contact a:hover {
  background-size: 100% 1px, 100% 1px;
}
@media (min-width: 1024px) {
  .contact {
    display: flex;
    gap: 0.75em;
  }
  .contact li {
    display: flex;
    gap: 0.75em;
  }
  .contact li:not(:last-child)::after {
    content: "·";
  }
}

.page {
  background-color: rgb(var(--bg-color));
  flex-direction: column;
  display: flex;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}
.page__backdrop {
  position: absolute;
  z-index: 1;
}
.page__backdrop img {
  position: absolute;
}
.page__backdropMobile {
  height: 100%;
  width: 100%;
}
.page__backdropParallax {
  display: none;
}
.page__main {
  color: #FFF;
  position: relative;
  z-index: 2;
}
.page__content {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
}
.page__text {
  display: flex;
  flex-direction: column;
  gap: 3em;
}
.page__header {
  display: flex;
  flex-direction: column;
  gap: 1em;
}
.page__intro {
  color: rgba(255, 255, 255, 0.85);
  line-height: 1.5;
}
.page__ctas {
  display: flex;
  gap: 0.5em;
}
@media (max-width: 1023px) {
  .page__backdrop {
    height: 150vw;
    position: fixed;
    width: 100vw;
  }
  .page__backdrop img {
    object-position: 72.5% 50%;
  }
  .page__main {
    background-image: linear-gradient(to bottom, rgb(var(--color-1)/0%) 25vw, rgb(var(--color-1)/100%) 150vw, rgb(0, 0, 0));
    min-height: 100vh;
    padding: 85vw 24px 40px 24px;
  }
  .page__logo {
    height: 3rem;
    pointer-events: none;
    position: fixed;
    top: 32px;
    transition: opacity 0.5s;
  }
  .is-hideLogo .page__logo {
    opacity: 0;
  }
  .page__ctas .btn {
    flex: 1;
  }
}
@media (min-width: 1024px) {
  .page__backdrop {
    inset: -24px;
  }
  .page__backdropMobile {
    display: none;
  }
  .page__backdropParallax {
    display: block;
    height: 100%;
    will-change: transform;
    width: 100%;
  }
  .page__main {
    background-image: linear-gradient(60deg, rgb(var(--color-1)/100%), rgba(0, 0, 0, 0.75) 50%, rgba(0, 0, 0, 0) 75%);
    color: #FFF;
    display: flex;
    flex: 1;
    gap: 64px;
    padding: 64px;
  }
  .page__content {
    flex: 0 0 50%;
    justify-content: space-between;
  }
  .page__intro {
    font-size: var(--font-size-l);
  }
  .page__logo {
    height: 4rem;
  }
  .page__contact {
    font-size: var(--font-size-s);
  }
}

@keyframes textSplitChar {
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
.textSplit {
  --duration: 1s;
}
.textSplit .word {
  --word-index: 0;
  display: inline-block;
  overflow: hidden;
  vertical-align: bottom;
  white-space: nowrap;
}
.textSplit .word span {
  display: inline-block;
  transform: translate3d(0, 1em, 0);
}
.textSplit .word::after {
  content: " ";
  white-space: pre;
}
.textSplit .char {
  display: inline-block;
  opacity: 0;
  transform: translate3d(0, 1em, 0);
}
.textSplit.is-textSplitVisible .word span {
  animation: textSplitChar var(--duration) cubic-bezier(0.5, 0, 0.25, 1) both;
  animation-delay: calc(250ms * var(--word-index) / var(--num-words));
}